@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
.page-body {
  margin: 0;
  padding: 0;
  font-family: "Pt sans", "Arial", sans-serif;
  font-display: swap;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
  background-color: #F2F2F2;
  min-height: 100%;
}
.page-body img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  white-space: nowrap;
  clip: rect(0 0 0 0);
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page-wrapper {
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}
@media (min-width: 768px) {
  .page-wrapper {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page-wrapper {
    width: 1200px;
  }
}
.page-header {
  position: relative;
}
.page-header__logo {
  position: absolute;
  left: 50%;
  top: 80%;
  height: 84px;
  margin-left: -52px;
  text-align: center;
}
@media (min-width: 768px) {
  .page-header__logo {
    top: 0;
    height: 84px;
