}
.button {
  display: block;
  font-size: 21px;
  line-height: 26px;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Pt sans", "Arial", sans-serif;
  font-display: swap;
  background-color: #81B3D2;
  color: #ffffff;
  padding: 15px 32px 15px 32px;
