    margin-left: 20px;
    margin-bottom: 17px;
  }
}
.photos__like {
  padding: 0;
  margin-right: 14px;
  background: none;
  border: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .photos__like {
    margin-right: 9px;
  }
}
.photos__wrapper-like {
  display: flex;
  width: 61px;
  align-items: center;
}
@media (min-width: 768px) {
  .photos__wrapper-like {
    margin-right: 23px;
    margin-top: 6px;
  }
}
@media (min-width: 1200px) {
  .photos__wrapper-like {
    margin-right: 24px;
    margin-top: 2px;
  }
}
.photos__like-image {
  fill: #000000;
  opacity: 0.3;
}
@media (min-width: 1200px) {
  .photos__like-image {
    fill: #ffffff;
    opacity: 1;
  }
}
.video {
  padding-top: 22px;
  padding-bottom: 54px;
}
@media (min-width: 768px) {
  .video {
    margin-top: 42px;
    padding-bottom: 58px;
  }
}
@media (min-width: 1200px) {
  .video {
    margin-top: 22px;
    padding-bottom: 102px;
  }
}
.video img {
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .video img {
    width: 648px;
    height: 500px;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .video img {
    width: 800px;
  }
}
@media (min-width: 768px) {
  .video__description {
    padding: 0 132px;
  }
}
@media (min-width: 1200px) {
  .video__description {
    padding: 0 332px;
  }
}
.video__controls {
  padding: 0 20px;
  margin-top: -45px;
}
@media (min-width: 768px) {
  .video__controls {
    position: absolute;
    margin: 0;
    padding: 0 60px;
    bottom: 0;
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .video__controls {
    padding: 0 200px;
  }
}
.video__timeline {
  position: relative;
  height: 45px;
  background-color: rgba(0, 0, 0, 0.6);
}
.video__timeline::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 235px;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .video__timeline::before {
    width: 390px;
    left: 44%;
  }
}
@media (min-width: 1200px) {
  .video__timeline::before {
    width: 540px;
    left: 45%;
  }
}
.video__timeline::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 22%;
  transform: translate(-50%, -50%);
  width: 9px;
  height: 9px;
  background-color: #ffffff;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .video__timeline::after {
    left: 32%;
  }
}
@media (min-width: 1200px) {
  .video__timeline::after {
    left: 26%;
  }
}
@media (min-width: 768px) {
  .video__timeline {
    height: 93px;
  }
}
.video__buttons {
  display: flex;
  align-items: center;
  background-color: #F2F2F2;
  height: 70px;
}
@media (min-width: 768px) {
  .video__buttons {
    position: absolute;
    bottom: 0;
    height: 92px;
    width: 100%;
    background-color: transparent;
  }
}
.video__button {
  background-color: transparent;
  border: transparent;
}
@media (min-width: 768px) {
  .video__button {
    fill: #ffffff;
  }
}
.video__button--replay {
  margin-left: 19px;
}
@media (min-width: 768px) {
  .video__button--replay {
    margin-left: 29px;
  }
}
.video__button--subtitles {
  margin-left: 121px;
}
@media (min-width: 768px) {
  .video__button--subtitles {
    margin-left: 462px;
  }
}
@media (min-width: 1200px) {
  .video__button--subtitles {
    margin-left: 612px;
  }
}
.video__button--fullscreen {
  margin-left: 18px;
}
.video__screen {
  position: relative;
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .video__screen {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .video__screen {
    width: 1200px;
  }
}
.video__frame {
  margin-top: 34px;
}
@media (min-width: 768px) {
  .video__frame {
    margin-top: 80px;
  }
}
.video__decoration {
  fill: #5C6870;
}
.video__decoration:hover {
  fill: #333333;
}
.video__decoration:active {
  fill: #333333;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .video__decoration {
    fill: #ffffff;
  }
  .video__decoration:hover {
    opacity: 0.6;
  }
  .video__decoration:active {
    opacity: 0.3;
  }
}
.popup {
  display: none;
  position: fixed;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  box-shadow: 0 4px 4px 2px rgba(0, 1, 1, 0.2);
  z-index: 4;
  text-align: center;
}
.popup--success {
  display: none;
  transform: translate(-50%, -50%);
  background-color: #E5E5E5;
}
@media (min-width: 768px) {
  .popup--success {
    width: 640px;
  }
}
.popup--fail {
  display: none;
}
@media (min-width: 768px) {
  .popup--fail {
    width: 400px;
  }
}
